declare global {
	interface Object {
		sce: string
		str: string
		toSource(): string
	}
	interface ObjectConstructor {
		construct<T>(cons: { new (...args: any[]): T }, args: any[]): T
		map<T, S, K extends string>(
			o: { [k in K]: T },
			f: (this: undefined, v: T, key: string, o: { [k in K]: T }) => S,
		): { [k in K]: S }
		map<T, S, K extends string, C>(
			o: { [k in K]: T },
			f: (this: C, v: T, key: string, o: { [k in K]: T }) => S,
			context: C,
		): { [k in K]: S }
	}
	// tslint:disable-next-line:class-name
	interface __NLAReadonlyArray<T> {
		/**
		 * this[this.length - 1]
		 */
		readonly last: T
	}
	interface Array<T> extends __NLAReadonlyArray<T> {
		/**
		 * this[this.length - 1]
		 */
		last: T

		map<U>(this: [T], callbackfn: (value: T, index: number, array: T[]) => U, thisArg?: any): [U]
		map<U>(this: Tuple2<T>, callbackfn: (value: T, index: number, array: T[]) => U, thisArg?: any): Tuple2<U>
		map<U>(this: Tuple3<T>, callbackfn: (value: T, index: number, array: T[]) => U, thisArg?: any): Tuple3<U>
		map<U>(this: Tuple4<T>, callbackfn: (value: T, index: number, array: T[]) => U, thisArg?: any): Tuple4<U>
		map<U>(this: Tuple5<T>, callbackfn: (value: T, index: number, array: T[]) => U, thisArg?: any): Tuple5<U>
		map<U>(this: Tuple6<T>, callbackfn: (value: T, index: number, array: T[]) => U, thisArg?: any): Tuple6<U>
		map<U>(this: Tuple7<T>, callbackfn: (value: T, index: number, array: T[]) => U, thisArg?: any): Tuple7<U>
		map<U>(this: Tuple8<T>, callbackfn: (value: T, index: number, array: T[]) => U, thisArg?: any): Tuple8<U>
	}
	interface ReadonlyArray<T> extends __NLAReadonlyArray<T> {}

	interface String {
		capitalizeFirstLetter(): string
		equals(x: any): boolean
	}
}
Object.map = function <T, S, K extends string>(
	o: { [k in K]: T },
	f: (v: T, key: string, o: { [k in K]: T }) => S,
	context: any = undefined,
): { [k in K]: S } {
	const result = {} as { [k in K]: S }
	for (const key in o) {
		result[key] = f.call(context, o[key], key, o)
	}
	return result
}
export function arrayHashCode(arr: any[]): int {
	let result = 0
	for (let i = 0; i < arr.length; i++) {
		result = (result * 31 + hashCode(arr[i])) | 0
	}
	return result | 0
}
export function hashCode(o: any) {
	if ('number' === typeof o || undefined === o) {
		return o | 0
	} else if (Array.isArray(o)) {
		return arrayHashCode(o)
	} else {
		return null === o ? 0 : o.hashCode()
	}
}

String.prototype.capitalizeFirstLetter = function () {
	return this.charAt(0).toUpperCase() + this.slice(1)
}
String.prototype.equals = function (x: any) {
	return this == x
}

export function SCE(o: any) {
	switch (typeof o) {
		case 'undefined':
			return 'undefined'
		case 'function':
			return o.toString()
		case 'number':
			return '' + o
		case 'string':
			return JSON.stringify(o)
		case 'object':
			if (null == o) {
				return 'null'
			} else {
				return o.sce
			}
		default:
			throw new Error()
	}
}
export function STR(o: any) {
	return o.str
}
Object.defineProperty(Object.prototype, 'sce', {
	get: function () {
		return this.toSource()
	},
})
Object.defineProperty(Object.prototype, 'str', {
	get: function () {
		return this.toString()
	},
})

