import { int, assertInts, assertNumbers, lerp, assert, MINUS } from './util'

export function arraySwap(arr: any[], i: int, j: int) {
	const temp = arr[i]
	arr[i] = arr[j]
	arr[j] = temp
}
export type ArrayLikeWriteAble<T> = {
	readonly length: int
	[index: number]: T
}
export function arrayCopy<T, A extends ArrayLikeWriteAble<T>>(
	src: ArrayLike<T>,
	sstart: int,
	dst: A,
	dstart: int,
	length: int,
): A {
	assertInts(sstart, dstart, length)
	dstart += length
	length += sstart
	while (length-- > sstart) {
		dst[--dstart] = src[length]
	}
	return dst
}
/**
 * Copies a number of items from one array to another, with a definable step size between items in the source and
 * destination array.
 *
 * @param src The source array.
 * @param sstart The location of the first item in the source array.
 * @param sstep The offset between items in the source array.
 * @param dst The destination array.
 * @param dstart The location of the first item in the destination array.
 * @param dstep The offset between items in the destination array.
 * @param count The number of items to copy.
 */
export function arrayCopyStep<T>(
	src: ArrayLike<T>,
	sstart: int,
	sstep: int,
	dst: ArrayLikeWriteAble<T>,
	dstart: int,
	dstep: int,
	count: int,
): void {
	let srcIndex = sstart + count * sstep
	let dIndex = dstart + count * dstep
	while (srcIndex > sstart) {
		dst[(dIndex -= dstep)] = src[(srcIndex -= sstep)]
	}
}

/**
 * Copies a number of contiguous, evenly-spaced blocks from one array to another.
 *
 * @param src The source array.
 * @param sstart The start of the first block in the source array.
 * @param sstep The offset from the start of one block to the start of the next block in the source array.
 * @param dst The destination array.
 * @param dstart The start of the first block in the destination array.
 * @param dstep The offset from the start of one block to the start of the next block in the destination array.
 * @param blockSize The length of one block.
 * @param blockCount The number of blocks to copy.
 */
export function arrayCopyBlocks<T>(
	src: ArrayLike<T>,
	sstart: int,
	sstep: int,
	dst: ArrayLikeWriteAble<T>,
	dstart: int,
	dstep: int,
	blockSize: int,
	blockCount: int,
): void {
	for (let i = 0; i < blockCount; i++) {
		arrayCopy(src, sstart + sstep * i, dst, dstart + dstep * i, blockSize)
	}
}
export function arrayRange(startInclusive: int, endExclusive: int, step: int = 1) {
	assertNumbers(startInclusive, step)
	//console.log(Math.ceil((endExclusive - startInclusive) / step))
	const arrLength = Math.ceil((endExclusive - startInclusive) / step)
	const result = new Array(arrLength) // '- startInclusive' so that chunk in the last row will also be selected, even
	// if the row is not complete
	for (let i = startInclusive, index = 0; index < arrLength; i += step, index++) {
		result[index] = i
	}
	return result
}

/**
 * Returns a number of evenly-spaced values between t0 and t1 (inclusive).
 * @param t0 First value.
 * @param t1 Last value.
 * @param count Total number of values.
 */
export function arraySamples(t0: number, t1: number, count: int = 64) {
	return arrayFromFunction(count, (i) => lerp(t0, t1, i / (count - 1)))
}

export function arrayFromFunction<T>(length: 1, f: (i: int, total: int) => T): [T]
export function arrayFromFunction<T>(length: 2, f: (i: int, total: int) => T): [T, T]
export function arrayFromFunction<T>(length: 3, f: (i: int, total: int) => T): [T, T, T]
export function arrayFromFunction<T>(length: 4, f: (i: int, total: int) => T): [T, T, T, T]
export function arrayFromFunction<T>(length: 5, f: (i: int, total: int) => T): [T, T, T, T, T]
export function arrayFromFunction<T>(length: int, f: (i: int, total: int) => T): T[]
export function arrayFromFunction<T>(length: int, f: (i: int, total: int) => T): T[] {
	assertNumbers(length)
	assert('function' == typeof f)
	const a = new Array(length)
	let elIndex = length
	while (elIndex--) {
		a[elIndex] = f(elIndex, length)
	}
	return a
}

/**
 * Return the element in the array for which f(el) is highest. There is no `withMin`, call withMax(x => -f(x))
 * instead.
 * @param f
 */
export function withMax<T>(arr: T[], f: (t: T, i: int, arr: T[]) => number): T | undefined {
	let i = arr.length,
		result = undefined,
		maxVal = -Infinity
	while (i--) {
		const el = arr[i],
			val = f(el, i, arr)
		if (val > maxVal) {
			maxVal = val
			result = el
		}
	}
	return result
}

/**
 * Returns the sum of the absolute values of the components of arr vector.
 * E.g. V(1, -2, 3) === abs(1) + abs(-2) + abs(3) === 1 + 2 + 3 === 6
 */
export function absSum(arr: number[]) {
	let i = arr.length
	let result = 0
	while (i--) {
		result += Math.abs(arr[i])
	}
	return result
}

export function emod<T>(arr: T[], i: int): T {
	return arr[i % arr.length]
}
export function sliceStep<T>(arr: T[], start: int, end: int, step: int, chunkSize = 1): T[] {
	assertNumbers(start, step)
	start < 0 && (start = arr.length + start)
	end <= 0 && (end = arr.length + end)
	const resultLength = Math.ceil((end - start) / step)
	const result = new Array(resultLength) // '- start' so that chunk in the last row
	// will also be selected, even if the row is
	// not complete
	let index = 0
	for (let i = start; i < end; i += step) {
		for (let j = i; j < Math.min(i + chunkSize, end); j++) {
			result[index++] = arr[j]
		}
	}
	assert(resultLength == index)
	return result
}
export function splicePure<T>(arr: T[], start = 0, deleteCount = 0, ...items: T[]): T[] {
	const arrayLength = arr.length
	const _deleteCount = deleteCount < 0 ? 0 : deleteCount
	let _start
	if (start < 0) {
		if (Math.abs(start) > arrayLength) {
			_start = 0
		} else {
			_start = arrayLength + start
		}
	} else if (start > arrayLength) {
		_start = arrayLength
	} else {
		_start = start
	}
	const newLength = arr.length - _deleteCount + items.length
	const result = new Array(newLength)
	let dst = newLength
	let src = arr.length
	while (src-- > _start + _deleteCount) {
		result[--dst] = arr[src]
	}
	src = items.length
	while (src--) {
		result[--dst] = items[src]
	}
	src = _start
	while (src--) {
		result[--dst] = items[src]
	}
	return result
}
export function arrayEquals(arr: any[], obj: any[]): boolean {
	if (arr === obj) return true
	if (Object.getPrototypeOf(obj) !== Array.prototype) return false
	if (arr.length !== obj.length) return false
	for (let i = 0; i < arr.length; i++) {
		if (!equals(arr[i], obj[i])) return false
	}
	return true
}
export function equals(a: any, b: any) {
	return 'object' === typeof a ? a.equals(b) : a === b
}

/**
 * arr.map(f).filter((x) => x)
 */
export function mapFilter<T, S>(arr: T[], f: (item: T, index: int, arr: T[]) => S): S[] {
	const length = arr.length
	const result = []
	for (let i = 0; i < length; i++) {
		if (i in arr) {
			const val = f(arr[i], i, arr)
			if (val) {
				result.push(val)
			}
		}
	}
	return result
}
export function clear<T>(arr: T[], ...newItems: T[]): T[] {
	return arr.splice(0, arr.length, ...newItems)
}
/**
 *
 * @returns function concat.apply([], arr)
 */
export function concatenated<T>(arr: T[]) {
	return Array.prototype.concat.apply([], arr)
}
export function min<T>(arr: number[]): number {
	let i = arr.length,
		max = Infinity
	while (i--) {
		const val = arr[i]
		if (max > val) max = val
	}
	return max
}
export function max(arr: number[]): number {
	// faster and no limit on array size, see https://jsperf.com/math-max-apply-vs-loop/2
	let i = arr.length,
		max = -Infinity
	while (i--) {
		const val = arr[i]
		if (max < val) max = val
	}
	return max
}
export function indexWithMax<T>(arr: T[], f: (e: T, i: int, arr: T[]) => number) {
	if (arr.length == 0) {
		return -1
	}
	let i = arr.length,
		result = -1,
		maxVal = -Infinity
	while (i--) {
		const val = f(arr[i], i, arr)
		if (val > maxVal) {
			maxVal = val
			result = i
		}
	}
	return result
}

export function sum(arr: number[]) {
	let i = arr.length
	let result = 0
	while (i--) {
		result += arr[i]
	}
	return result
}

export function sumInPlaceTree(arr: number[]) {
	if (0 == arr.length) return 0
	let l = arr.length
	while (l != 1) {
		const lHalfFloor = Math.floor(l / 2)
		const lHalfCeil = Math.ceil(l / 2)
		for (let i = 0; i < lHalfFloor; i++) {
			arr[i] += arr[i + lHalfCeil]
		}
		l = lHalfCeil
	}
	return arr[0]
}

export function unique<T>(arr: T[]): T[] {
	const uniqueSet = new Set<T>(arr)
	return Array.from(uniqueSet)
}

export function remove<T>(arr: T[], o: T): boolean {
	const index = arr.indexOf(o)
	if (index != -1) {
		arr.splice(index, 1)
		return true
	}
	return false
}

export function removeIndex<T>(arr: T[], i: int): T {
	const result = arr[i]
	arr.splice(i, 1)
	return result
}
export function bagRemoveIndex<T>(arr: T[], i: int): T {
	const result = arr[i]
	if (i == arr.length - 1) {
		arr.pop()
	} else {
		arr[i] = arr.pop()!
	}
	return result
}

export function removeMatch<T>(arr: T[], matcher: (el: T) => boolean) {
	const index = arr.findIndex(matcher)
	if (-1 != index) {
		return removeIndex(arr, index)
	}
}

export function removeAll<T>(arr: T[], o: T[]): void {
	let i = o.length
	while (i--) {
		remove(arr, o[i])
	}
}

export function toggle<T>(arr: T[], o: T) {
	const index = arr.indexOf(o)
	if (index != -1) {
		arr.splice(index, 1)
		return false
	} else {
		arr.push(o)
		return true
	}
}
export function bagToggle<T>(arr: T[], o: T) {
	const index = arr.indexOf(o)
	if (index != -1) {
		bagRemoveIndex(arr, index)
		return false
	} else {
		arr.push(o)
		return true
	}
}

export function binaryIndexOf<T>(arr: T[], searchElement: T, cmp = (a: any, b: any) => a - b): int {
	let minIndex = 0
	let maxIndex = arr.length - 1
	let currentIndex
	let currentElement

	while (minIndex <= maxIndex) {
		currentIndex = ((minIndex + maxIndex) / 2) | 0
		currentElement = arr[currentIndex]

		if (cmp(currentElement, searchElement) < 0) {
			minIndex = currentIndex + 1
		} else if (cmp(currentElement, searchElement) > 0) {
			maxIndex = currentIndex - 1
		} else {
			return currentIndex
		}
	}

	return -minIndex - 1
}

export function binaryInsert<T>(arr: T[], el: any, cmp: (a: T, b: T) => number = MINUS as any) {
	let minIndex = 0
	let maxIndex = arr.length
	let currentIndex
	let currentElement

	while (minIndex < maxIndex) {
		currentIndex = ~~((minIndex + maxIndex) / 2)
		currentElement = arr[currentIndex]

		if (cmp(currentElement, el) < 0) {
			minIndex = currentIndex + 1
		} else {
			maxIndex = currentIndex
		}
	}

	arr.splice(minIndex, 0, el)
}

export function firstUnsorted<T>(arr: T[], cmp: (a: T, b: T) => number) {
	for (let i = 1; i < arr.length; i++) {
		if (cmp(arr[i - 1], arr[i]) > 0) return i
	}
	return -1
}
export function getLast<T>(arr: ArrayLike<T>): T {
	return arr[arr.length - 1]
}
export function setLast<T>(arr: T[], val: T): T {
	return (arr[arr.length - 1] = val)
}

export function removeIndexes<T>(arr: T[], indexes: int[]): T[] {
	indexes.sort((a, b) => a - b)
	if (0 === indexes.length) return arr
	if (1 === indexes.length) {
		arr.splice(indexes[0], 1)
		return arr
	}
	let dstPos = indexes[0]
	let nextSkip = indexes[0]
	let indexesPos = 0
	for (let srcPos = indexes[0]; srcPos < arr.length; srcPos++) {
		if (srcPos !== nextSkip) {
			arr[dstPos++] = arr[srcPos]
		} else {
			indexesPos++
			if (indexesPos < indexes.length) {
				nextSkip = indexes[indexesPos]
			} else {
				arr.splice(dstPos, srcPos + 1 - dstPos)
				return arr
			}
		}
	}
	throw new Error('illegal state')
}
