/**
 * Calculate the integral of f in the interval [-1;1].
 * @param f
 */
export function glq24_11(f: (x: number) => number): number {
	return sumInPlaceTree(arrayFromFunction(24, (i) => gaussLegendre24Weights[i] * f(gaussLegendre24Xs[i])))
}
export function glqInSteps(f: (x: number) => number, startT: number, endT: number, steps: int) {
	const dt = (endT - startT) / steps
	return (
		(sumInPlaceTree(arrayFromFunction(steps, (i) => glq24_11((t) => f(startT + dt * i + ((t + 1) / 2) * dt)))) /
			2) *
		dt
	)
}

export function midpointRuleQuadrature(
	f: (x: number) => number,
	startT: number,
	endT: number,
	steps: int = 32,
): number {
	const dt = (endT - startT) / steps
	return sumInPlaceTree(arrayFromFunction(steps, (i) => startT + dt / 2 + dt * i).map(f)) * dt
}

/**
 * incomplete elliptic integral of the first kind
 * EllipticF(phi, k2) = INT[0; phi] 1 / sqrt(1 - k2 * sin²(phi)) dphi
 */
export function EllipticF(phi: number, k2: number): number {
	return gaussLegendreQuadrature24((phi) => Math.pow(1 - k2 * Math.sin(phi) ** 2, -0.5), 0, phi)
}

/**
 * incomplete elliptic integral of the second kind
 * EllipticE(phi, k2) = INT[0; phi] sqrt(1 - k2 * sin²(phi)) dphi
 */
export function EllipticE(phi: number, k2: number): number {
	return gaussLegendreQuadrature24((phi) => Math.pow(1 - k2 * Math.sin(phi) ** 2, 0.5), 0, phi)
}

export function getIntervals(ts: number[], min: number, max: number): [number, number][] {
	ts.sort((a, b) => a - b)
	if (!eq(ts[0], min)) {
		ts.splice(0, 0, min)
	}
	if (!eq(ts.last, max)) {
		ts.push(max)
	}
	return arrayFromFunction(ts.length - 1, (i) => [ts[i], ts[i + 1]] as [number, number])
}

export function isCCW(vertices: V3[], normal: V3) {
	const dsa = doubleSignedArea(vertices, normal)
	assert(0 != dsa)
	return dsa < 0
}
export function doubleSignedArea(vertices: V3[], normal: V3) {
	assert(!normal.likeO(), '!normal.likeO()')
	const absMaxDim = normal.maxAbsDim()
	// order is important, coord0 and coord1 must be set so that coord0, coord1 and maxDim span a right-hand coordinate
	// system var [coord0, coord1] = [['y', 'z'], ['z', 'x'], ['x', 'y']][maxAbsDim]
	const doubleSignedArea = vertices
		.map((v0, i, vertices) => {
			const v1 = vertices[(i + 1) % vertices.length]
			//return (v1[coord0] - v0[coord0]) * (v1[coord1] + v0[coord1])
			switch (absMaxDim) {
				case 0:
					return (v1.y - v0.y) * (v1.z + v0.z)
				case 1:
					return (v1.z - v0.z) * (v1.x + v0.x)
				case 2:
					return (v1.x - v0.x) * (v1.y + v0.y)
			}
		})
		.reduce((a, b) => a + b)
	return snap(doubleSignedArea * Math.sign(normal.e(absMaxDim)), 0)
}

export function newtonIterate(
	f: (x: number[]) => number[],
	x: number[],
	steps: int = 4,
	EPSILON?: number,
	stepize = 1,
) {
	EPSILON = EPSILON || 1e-8

	for (let i = 0; i < steps; i++) {
		const fx = f(x)
		const dfdx = Matrix.jacobi(f, x, fx, EPSILON)
		assert(!dfdx.isZero())
		const dx = dfdx.solveLinearSystem(new Vector(new Float64Array(fx))).v
		assert(!isNaN(dx[0]))
		//console.log('fx / dfdx', fx / dfdx)
		for (let j = 0; j < x.length; j++) x[j] -= dx[j] * stepize
	}
	return x
}

export function newtonIterate1d(
	f: (x: number) => number,
	xStart: number,
	steps: number | undefined,
	df: (x: number) => number,
): number
export function newtonIterate1d(f: (x: number) => number, xStart: number, steps: number, EPSILON?: number): number
export function newtonIterate1d(
	f: (x: number) => number,
	xStart: number,
	steps: number = 8,
	epsOrDf: number | ((x: number) => number) = 1e-8,
): number {
	if ('number' === typeof epsOrDf) {
		let x = xStart

		for (let i = 0; i < steps; i++) {
			const fx = f(x)
			const dfdx = (f(x + epsOrDf) - fx) / epsOrDf
			//console.log('fx / dfdx', fx / dfdx)
			x = x - fx / dfdx
		}
		return x
	} else {
		return newtonIterateWithDerivative(f, xStart, steps, epsOrDf)
	}
}
export function newtonIterateWithDerivative(
	f: (x: number) => number,
	xStart: number,
	steps: number = 4,
	df: (x: number) => number,
) {
	let x = xStart
	for (let i = 0; i < steps; i++) {
		const fx = f(x)
		const dfdx = df(x)
		if (isNaN(fx) || isNaN(dfdx)) {
			console.log()
			//console.log('fx / dfdx', fx / dfdx)
		}
		x = x - fx / dfdx
		if (isNaN(fx)) {
			console.log()
			//console.log('fx / dfdx', fx / dfdx)
		}
	}
	return x
}
export function newtonIterateWithDerivative2(
	fAndDf: (x: number) => [number, number],
	xStart: number,
	steps: number = 8,
	xMin = -Infinity,
	xMax = Infinity,
) {
	assert(steps % 1 == 0)
	let x = xStart
	for (let i = 0; i < steps; i++) {
		const [fx, dfdx] = fAndDf(x)
		if (isNaN(fx) || isNaN(dfdx)) {
			console.log()
			//console.log('fx / dfdx', fx / dfdx)
		}
		x = x - fx / dfdx
		if (0 == dfdx || x < xMin || xMax < x) {
			return undefined
		}
	}
	return x
}
export function newtonIterateSmart(
	f: (x: number) => number,
	xStart: number,
	steps: number = 4,
	df: (x: number) => number,
	mindf = 1e-6,
) {
	let x = xStart
	for (let i = 0; i < steps; i++) {
		const fx = f(x)
		const dfdx = df(x)
		if (Math.abs(dfdx) < mindf && Math.abs(fx) < mindf) {
			return newtonIterate1d(df, x, steps, df)
		}
		if (isNaN(fx) || isNaN(dfdx)) {
			console.log()
			//console.log('fx / dfdx', fx / dfdx)
		}
		x = x - fx / dfdx
		if (isNaN(fx)) {
			console.log()
			//console.log('fx / dfdx', fx / dfdx)
		}
	}
	return x
}
export function newtonIterate2d(
	f1: (s: number, t: number) => number,
	f2: (s: number, t: number) => number,
	sStart: number,
	tStart: number,
	steps?: number,
): V3 | undefined {
	const EPSILON = 1e-6
	steps = steps || 4
	let s = sStart,
		t = tStart,
		f1ts,
		f2ts
	do {
		/*
         | a b |-1                   |  d -b |
         | c d |   = 1 / (ad - bc) * | -c  a |
         */
		f1ts = f1(s, t)
		f2ts = f2(s, t)
		/*
         let df1s = (f1(s + EPSILON, t) - f1ts) / EPSILON, df1t = (f1(s, t + EPSILON) - f1ts) / EPSILON,
         df2s = (f2(s + EPSILON, t) - f2ts) / EPSILON, df2t = (f2(s, t + EPSILON) - f2ts) / EPSILON
         let det = df1s * df2t - df1t * df2s
         s = s - ( df2t * f1ts - df1t * f2ts) / det
         t = t - (-df2s * f1ts + df1s * f2ts) / det
         */
		// TODO: is this even more accurate?
		const df1s = f1(s + EPSILON, t) - f1ts,
			df1t = f1(s, t + EPSILON) - f1ts,
			df2s = f2(s + EPSILON, t) - f2ts,
			df2t = f2(s, t + EPSILON) - f2ts
		const det = (df1s * df2t - df1t * df2s) / EPSILON
		const ds = (df2t * f1ts - df1t * f2ts) / det
		const dt = (-df2s * f1ts + df1s * f2ts) / det
		s -= ds
		t -= dt
	} while (--steps && f1ts ** 2 + f2ts ** 2 > NLA_PRECISION)
	if (f1ts ** 2 + f2ts ** 2 > NLA_PRECISION) {
		console.log(f1ts ** 2 + f2ts ** 2)
		return undefined
	}
	return new V3(s, t, 0)
}
export function newtonIterate2dWithDerivatives(
	f: (s: number, t: number) => number,
	g: (s: number, t: number) => number,
	sStart: number,
	tStart: number,
	steps: int,
	dfds: (s: number, t: number) => number,
	dfdt: (s: number, t: number) => number,
	dgds: (s: number, t: number) => number,
	dgdt: (s: number, t: number) => number,
): V3 | undefined {
	steps = steps || 4
	let s = sStart,
		t = tStart
	let f1ts, f2ts
	do {
		/*
         | a b |-1                   |  d -b |
         | c d |   = 1 / (ad - bc) * | -c  a |
         */
		f1ts = f(s, t)
		f2ts = g(s, t)
		const df1s = dfds(s, t),
			df1t = dfdt(s, t),
			df2s = dgds(s, t),
			df2t = dgdt(s, t)
		// TODO: is this even more accurate?
		const det = df1s * df2t - df1t * df2s
		const ds = (df2t * f1ts - df1t * f2ts) / det
		const dt = (-df2s * f1ts + df1s * f2ts) / det
		s -= ds
		t -= dt
	} while (--steps && f1ts * f1ts + f2ts * f2ts > NLA_PRECISION / 32)
	if (!steps) {
		//console.log(f1ts * f1ts + f2ts * f2ts)
		return undefined
	}
	return V(s, t, 0)
}

export function getRoots(
	f: (x: number) => number,
	a: number,
	b: number,
	stepSize: number,
	df: (x: number) => number,
): number[] {
	const results: number[] = []
	for (let startT = a; startT <= b; startT += stepSize) {
		const dt = stepSize * Math.abs(df(startT))
		if (Math.abs(f(startT)) <= dt) {
			//const t = newtonIterate1d(f, startT, 16)
			let t = newtonIterateWithDerivative(f, startT, 16, df)
			if (!eq0(f(t)) || eq0(df(t))) {
				t = newtonIterate1d(df, startT, 16)
				// const a = startT - dt,
				// 	b = startT + dt
				//if (f(a) * f(b) < 0) {
				//    t = bisect(f, a, b, 16)
				//} else if (df(a) * df(b) < 0) {
				//    t = bisect(df, a, b, 16)
				//}
			}
			if (eq0(f(t)) && !results.some((r) => eq(r, t))) {
				results.push(t)
			}
		}
	}
	return results
}
